export function AddAtendimento () {

const {register, handleSubmit, errors, setValue ,getValues} = useForm();

  

  let navigate = useNavigate();

        return (<>
           <Sidebar/>      
            
       <div style={{ flexDirection: "column", margin:'0px',padding:'0px', width :'100%'}}>
                <Topbar title="Novo Atendimento"/>
               
                <ContainerGeral >
                <form onSubmit={handleSubmit}>
                    
                   
                    
       
        <div style={{flexDirection: "row", justifyContent: "space-between",display:'flex', alignItems:"center"}}>
          
          <Button style={{width:'35%' }} color="danger" onClick={()=> navigate('/atendimentos')}>
            Cancelar
          </Button>
          <Button style={{width:'35%' }} color="success" onClick={handleSubmit}>
           Adicionar Atendimento
          </Button>
        </div>
    
      </form>
                </ContainerGeral>


                </div>
            
            </>
        );
    }
export default AddAtendimento